import MagnifyingGlass from './magnifying-glass';
import PhotoDetail from './photo-detail';
import Sortable from './sortable';
import SVG from './svg';
import SVGRadialMenu from './svg-lib/svg-radial-menu';
import Editor from './editor/editor';
import AnalyticalTable from './analytical-table';
import Keywords from './keywords';
import EditorTagsPlaceholder from './editor-tags-placeholder';
import TestQuiz from './test-quiz';
import GenericTable from './generic-table';
import FullscreenToggle from './fullscreen';
import UserText from './user-text';
import AudioPlayerNormal from './audio/audio-player-normal';
import AudioPlayerSimple from './audio/audio-player-simple';
import FakeMap from './fake-map';
// import actionTypes from '../store/store-constants';
// import storeGlobal from '../store/store-global';

const windowLoadComponents = () => {
  console.log('windowLoad:components');

  // Audio player - normal
  const $audioPlayerNormals = document.querySelectorAll('[data-player-audio="normal"]');
  if ($audioPlayerNormals.length > 0) {
    Array.from($audioPlayerNormals).forEach(($audioPlayerNormal) => {
      const audioPlayerNormal = new AudioPlayerNormal($audioPlayerNormal);
    });
  }

  // Audio player — simple
  const $audioPlayerSimples = document.querySelectorAll('[data-player-audio="simple"]');
  if ($audioPlayerSimples.length > 0) {
    Array.from($audioPlayerSimples).forEach(($audioPlayerSimple) => {
      const audioPlayerSimple = new AudioPlayerSimple($audioPlayerSimple);
    });
  }

  // Fullscreen
  const $fullscreenToggles = document.querySelectorAll('[data-fullscreen="toggle"]');
  if ($fullscreenToggles.length > 0) {
    Array.from($fullscreenToggles).forEach(($fullscreenToggle) => {
      const fullscreenToggle = new FullscreenToggle($fullscreenToggle);
    });
  }

  // Magnifying glass
  const $magnifyingGlass = document.querySelectorAll('[data-lupa-target]');
  if ($magnifyingGlass.length > 0) {
    for (let i = 0; i < $magnifyingGlass.length; i += 1) {
      const magnifyingGlass = new MagnifyingGlass($magnifyingGlass[i]);
    }
  }

  // Photo detail
  const $photoDetails = document.querySelectorAll('[data-photo-detail-target]');
  if ($photoDetails.length > 0) {
    for (let i = 0; i < $photoDetails.length; i += 1) {
      const photoDetail = new PhotoDetail($photoDetails[i]);
    }
  }

  // Fake Map
  const $fakeMaps = document.querySelectorAll('[data-fake-map="target"]');
  if ($fakeMaps.length > 0) {
    for (let i = 0; i < $fakeMaps.length; i += 1) {
      const fakeMap = new FakeMap($fakeMaps[i]);
    }
  }


  //
  // load those only in working mode
  if (HISTORYLAB.import.done !== true) {
    // User texts
    const $userTexts = document.querySelectorAll('[data-textarea-target=""]');
    if ($userTexts.length > 0) {
      Array.from($userTexts).forEach(($userText) => {
        const userText = new UserText($userText);
      });
    }

    // Editor
    const $editors = document.querySelectorAll('[data-editor]');
    if ($editors.length > 0) {
      Array.from($editors).forEach(($editor) => {
        const editor = new Editor($editor);
      });
    }

    // Test Quizes
    const $testQuizs = document.querySelectorAll('[data-target-test-quiz]');
    if ($testQuizs.length > 0) {
      Array.from($testQuizs).forEach(($testQuiz) => {

        const settings = document.querySelector(`[data-test-quiz-id-reference="${$testQuiz.getAttribute('data-test-quiz-id')}"]`);

        const testQuiz = new TestQuiz(
          $testQuiz.getAttribute('data-test-quiz-id'),
          $testQuiz,
          JSON.parse(settings.getAttribute('data-module-settings')),
        );
        testQuiz.init();

      });

    }

    // Analytical tables
    const $analyticalTables = document.querySelectorAll('[data-target-analytical-table]');

    if ($analyticalTables.length > 0) {
      Array.from($analyticalTables).forEach(($analyticalTable) => {

        const settings = document.querySelector(`[data-analytical-table-id-reference="${$analyticalTable.getAttribute('data-analytical-table-id')}"]`);
        const analyticalTable = new AnalyticalTable(
          $analyticalTable.getAttribute('data-analytical-table-id'),
          $analyticalTable,
          JSON.parse(settings.getAttribute('data-module-settings')),
        );
        analyticalTable.init();

      });

    }

    // Generic tables
    const $genericTables = document.querySelectorAll('[data-target-generic-table]');
    if ($genericTables.length > 0) {
      Array.from($genericTables).forEach(($genericTable) => {

        const settings = document.querySelector(`[data-generic-table-id-reference="${$genericTable.id}"]`);
        const genericTable = new GenericTable(
          $genericTable,
          JSON.parse(settings.getAttribute('data-module-settings')),
        );
        genericTable.init();

      });
    }

    // SVG + Radial menu
    const $svgs = document.querySelectorAll('[data-svg-target]');
    if ($svgs.length > 0) {

      // radial menu
      const $radialMenu = document.querySelector('[data-radial-menu]');
      if ($radialMenu) {
        const radialMenu = new SVGRadialMenu($radialMenu, $svgs);
      }

      // svgs
      for (let i = 0; i < $svgs.length; i += 1) {
        const svg = new SVG($svgs[i]);
      }
    }

    // Sortable
    const $sortables = document.querySelectorAll('[data-sortable-target]');
    if ($sortables.length > 0) {
      for (let i = 0; i < $sortables.length; i += 1) {
        const sortable = new Sortable($sortables[i]);
      }
    }

    // Keywords selection
    const $keywords = document.querySelectorAll('[data-keywords]');
    if ($keywords.length > 0) {
      Array.from($keywords).forEach(($keyword) => {
        const settings = ($keyword.hasAttribute('data-module-settings')) ? $keyword.getAttribute('data-module-settings') : {};
        const keywords = new Keywords(
          $keyword,
          JSON.parse(settings),
        );
        keywords.init();
      });
    }

    // Editor tags placeholders
    const $editorTagsPlaceholders = document.querySelectorAll('.editor-tags-placeholder');
    if ($editorTagsPlaceholders.length > 0) {
      Array.from($editorTagsPlaceholders).forEach((item) => {
        const editorTagsPlaceholder = new EditorTagsPlaceholder(item);
        editorTagsPlaceholder.init();
      });
    }

  }

};


export default windowLoadComponents;
