import Observer from './observer';
import actionTypes from './store-constants';

/*
const tagContainer: Object = {
  editorId?: number,
  id: string,
  tag: HTMLElement,
  data?: Object
}
*/


class StoreKeywords extends Observer {

  constructor() {

    super();

    /* here the global space for storing objects and data */
    this._data = {};

  }


  change(keywordsId, newItems, newItemsHTML) {

    if (!this._data[keywordsId]) {
      this._data[keywordsId] = {};
    }

    this._data[keywordsId].items = newItems;
    this._data[keywordsId].itemsHTML = newItemsHTML;

    store.trigger(actionTypes.keywords.CHANGE, {
      action: {
        id: actionTypes.keywords.id,
        type: actionTypes.keywords.CHANGE,
        data: {
          keywordsId: keywordsId,
          newItems: newItems,
          newItemsHTML: newItemsHTML
        },
      },

    });

  }

  export() {
    return this._data;
  }

}

const store = new StoreKeywords();
export default store;
