import { __getHTML } from '../lib/utils';

import store from '../store/store';
import actionTypes from '../store/store-constants';

// zoom the image
export default class EditorTagsPlaceholder {
  constructor(element, opts = {}) {

    this.$element = element;
    this.id = this.$element.getAttribute('id');

    this.opts = Object.assign({}, EditorTagsPlaceholder.defaults, opts);

    this.commandNames = JSON.parse(this.$element.getAttribute(this.opts.itemSubscribeAttributeClassName));
    this.$itemsContainer = this.$element.getElementsByClassName(this.opts.itemsContainerClassName)[0];

  }

  init() {

    this._registerStoreHandlers();

  }


  _registerStoreHandlers() {

    store.on(actionTypes.editor.CHANGE, (event, payload) => {

      const action = payload.action;
      const data = payload.action.data;
      const tagContainer = data.tagContainer;

      if (this.commandNames.indexOf(tagContainer.tagName) > -1) {

        const tags = store.getTagsBy({key: 'tagName', value: this.commandNames});

        let content = "";

        tags.forEach((tag) => {

          content += __getHTML( tag.tag );

        });

        this.$itemsContainer.innerHTML = content;

      }

  });

  }



}





EditorTagsPlaceholder.defaults = {
  itemsContainerClassName: "editor-tags-placeholder-content",
  itemSubscribeAttributeClassName: 'data-placeholder-editor-subscribe'

};
