import { __remove } from '../lib/utils';
import storeGlobal from '../store/store-global';
import actionTypes from '../store/store-constants';
import Component from './component';


export default class GenericTable extends Component {

  constructor($element, opts) {
    super($element);

    this.id = this.target.id;
    this.opts = Object.assign({}, GenericTable.defaults, opts);

    this.rowIndex = 0;

    this.$tableResultBody = this.target.getElementsByClassName('table-body')[0];

    this.dataSettings = this.opts.data;

    this.cache = {
      tasksToComplete: 0,
      tasksCompleted: 0,
      tasks: [],
    };
  }

  init() {

    if (this.dataSettings.source === 'store') {
      this._registerStoreHandlers();
    }

    // this._registerHandlers();
  }

  _registerStoreHandlers() {

    // register store event handler
    // in tabulkaObecna JSON settings, subscribe is an array, here we need a string of strings separated by spaces
    storeGlobal.on(this.dataSettings.subscribe.join(' '), (event, payload) => {
      const action = payload.action;
      const data = payload.data;

      switch (action.type) {

        case actionTypes[event].ADD: {
          this.$tableResultBody.insertAdjacentHTML('beforeend', GenericTable._getRowHTML(data));

          const $allRows = this.$tableResultBody.querySelectorAll('.table-row');
          const $lastRow = $allRows[$allRows.length - 1];

          const $newSelects = $lastRow.querySelectorAll('select');

          this.cache.tasksToComplete += $newSelects.length;

          Array.from($newSelects).forEach(($select) => {
            this.cache.tasks.push({ answered: false });
            const taskNumber = this.cache.tasks.length - 1;

            $select.addEventListener('change', () => {
              if (this.cache.tasks[taskNumber].answered === false) {
                this.cache.tasksCompleted += 1;
                this.cache.tasks[taskNumber].answered = true;
              }
              if (this.cache.tasksToComplete === this.cache.tasksCompleted) {
                // show OK button or feedback
                this.showFeedback();
              }
            });
          });
          break;
        }
        case actionTypes[event].DELETE: {
          const $row = document.getElementById(`${data.id}_table`);
          __remove($row);
          this.cache.tasksToComplete -= $row.querySelectorAll('select').length;
          this.cache.tasksCompleted -= $row.querySelectorAll('select').length;

          if (this.cache.tasksToComplete <= this.cache.tasksCompleted) {
            // show OK button or feedback
            this.showFeedback();
          }

          break;
        }
        default:
          break;
      }

    });

  }


}

GenericTable.defaults = {};

// data.data.text: editor text name
// data.data.vyraz selected text option text value
GenericTable._getRowHTML = (data) => {
  const tableRowTemplate = `
    <div class="table-row" id="${data.id}_table">
      <div class="cell">${data.data.text}</div>
      <div class="cell">${data.data.vyraz}</div>
      <div class="cell">
        <select id="${data.id}-select-osoba" name="osoba" data-save="value">
          <option>Vyberte osobu</option>
          <option>já</option>
          <option>ty</option>
          <option>on/ona/ono</option>
          <option>my</option>
          <option>vy</option>
          <option>oni/ony/ona</option>
          <option>infinitiv</option>
        </select>
      </div>
      <div class="cell">
        <select id="${data.id}-select-zpusob" name="zpusob" data-save="value">
          <option>Vyberte způsob</option>
          <option>oznamovací</option>
          <option>podmiňovací</option>
          <option>rozkazovací</option>
          <option id="select-zpusob-infinitiv">infinitiv</option>
        </select>
      </div>
    </div>
  `;

  return tableRowTemplate;

};
