import Component from '../component';

export default class AudioPlayer extends Component {
  constructor($element) {
    super($element);

    this.$navButtons = Array.from(document.querySelectorAll('[data-nav-button]'));
    this.$navButtons.forEach(($navButton) => {
      $navButton.addEventListener('click', () => {
        AudioPlayer.stopPlayingAudioPlayers();
      });
    });
  }

  static addEventListeners($item, showFeedback) {
    const $audio = $item.querySelector('audio');
    const $playButton = $item.querySelector('[data-player-control]');
    const $timeLeft = $item.querySelector('[data-audio-time-left]');

    $playButton.addEventListener('click', (event) => {
      if ($playButton.getAttribute('data-player-control') === 'paused') {
        AudioPlayer.stopPlayingAudioPlayers();
        $audio.play();
        $playButton.setAttribute('data-player-control', 'playing');
      } else {
        AudioPlayer.stopPlayingAudioPlayers();
      }

      showFeedback();

      event.stopPropagation();
    }, false);

    // do not propagate aduio control events higher into the dom
    $playButton.addEventListener('mousedown', (event) => {
      event.stopPropagation();
    }, false);

    $audio.addEventListener('timeupdate', () => {
      $timeLeft.innerText = AudioPlayer.getSecondsMinutesFormat($audio.currentTime);
    });

    $audio.addEventListener('ended', () => {
      $playButton.setAttribute('data-player-control', 'paused');
    });
  }

  static stopPlayingAudioPlayers() {
    // pause all other playing audio tracks
    const $players = document.querySelectorAll('[data-player-audio]');
    if ($players.length > 0) {
      Array.from($players).forEach(($player) => {
        const $audio = $player.querySelector('audio');
        if ($audio && !$audio.paused) {
          $audio.pause();
          $player.querySelector('[data-player-control]').setAttribute('data-player-control', 'paused');
        }
      });
    }
  }

  static getSecondsMinutesFormat(seconds) {
    const duration = {
      seconds: Math.floor(seconds),
      minutes: Math.floor(seconds / 60),
    };

    duration.seconds = (duration.seconds - (duration.minutes * 60)).toString(10);

    return `${duration.minutes}:${(`0${duration.seconds}`).slice(-2)}`;
  }

}
