// [data-fullscreen]
import Component from './component';

class Fullscreen extends Component {
  constructor($element) {
    super($element);

    this.$fullscreen = document.querySelector('[data-fullscreen="target"]');
    this.$fullscreenImage = this.$fullscreen.querySelector('[data-fullscreen="image"]');

    this.window = {};
    this.image = {};

    this.window.width = window.innerWidth;
    this.window.height = window.innerHeight;
    this.window.ratio = this.window.width / this.window.height;

    this.$fullscreenImage.onload = () => {
      this.image.width = this.$fullscreenImage.naturalWidth;
      this.image.height = this.$fullscreenImage.naturalHeight;
      this.image.ratio = this.image.width / this.image.height;

      if (this.image.ratio >= this.window.ratio) {
        this.$fullscreenImage.style.width = '100%';
        this.$fullscreenImage.style.height = 'auto';
      } else {
        this.$fullscreenImage.style.width = 'auto';
        this.$fullscreenImage.style.height = `${this.window.height}px`;
      }
    };

  }
}

export default class FullscreenToggle extends Fullscreen {
  constructor($element) {
    super($element);

    this.target.addEventListener('click', (event) => {
      this.$fullscreenImage.src = this.target.nextElementSibling.src;
      this.$fullscreen.classList.toggle('hidden');

      this.showFeedback();

      event.preventDefault();
    });

  }
}
