export default class Observer {

  constructor() {
    this._events = [];
  }

  /**
   * Add event listener
   *
   * {String} events - e.g. 'my-event-1 my-event-2'
   * {Function} fn - event listener
   */
  on(events, fn) {

    events = this._split(events);

    events.forEach((event) => {

      if (!this._events[event]) {
        this._events[event] = [];
      }

      this._events[event].push(fn);
    });

  }

  off(events, fn) {

    events = this._split(events);

    events.forEach((event) => {

      if (!this._events[event]) { return; }

      this.events[event] = this._events[event].filter((current_fn) => {
        return current_fn !== fn;
      });

    });


  }

  trigger(events, args) {

    events = this._split(events);

    events.forEach((event) => {

      if (!this._events[event]) { return; }

      this._events[event].forEach((fn) => {
        fn.call(this, event, args);
      });

    });

  }

  _split(events) {
    return events.replace(/^\s+|\s+$/g, '').split(/\s+/);
  }

}
