import { __hasClass, __getHTML, __remove } from '../lib/utils';
import store from '../store/store';
import actionTypes from '../store/store-constants';
import Component from './component';


export default class AnalyticalTable extends Component {

  constructor(id, element, opts) {
    super(element);

    this.id = id;

    this.opts = Object.assign({}, AnalyticalTable.defaults, opts);

    this.isAdvanced = this.opts.isAdvanced;

    this.rowIndex = 0;

    this.tableResult = this.target.getElementsByClassName('table-result')[0];
    this.tableResultBody = this.tableResult.getElementsByClassName('table-body')[0];
    this.tableSelectedItems = this.target.getElementsByClassName('table-selected-items')[0];
    this.tableSelectedItemsBody = this.tableSelectedItems.getElementsByClassName('table-body')[0];

    this.activeDragged = null;

    // choose the HTML for a row according to whether we work with the advanced version of a table
    this._getRowHTML = (this.isAdvanced) ? AnalyticalTable._getAdvancedRowHTML : AnalyticalTable._getRowHTML;

  }

  init() {
    this._registerStoreHandlers(this.tableResultBody, this.tableSelectedItemsBody);
    this._registerHandlers();
  }

  _registerStoreHandlers(tableResultBody, tableSelectedItemsBody) {

    store.on(actionTypes.editor.CHANGE, (event, payload) => {

      const action = payload.action;
      let addedTagContainer;
      let $node;
      // let deletedItem;

      switch (action.type) {

        case actionTypes.editor.ADD_TAG:

          addedTagContainer = action.data.tagContainer;

          switch (addedTagContainer.tagName) {

            case this.opts.casti[1].sloupce[0].tag:

              tableResultBody.insertAdjacentHTML('beforeend', this._getRowHTML(__getHTML(addedTagContainer.tag), addedTagContainer.id));


              // TODO: fix this
              if (this.opts.letSecondCaseRunIfSame || this.opts.casti[1].sloupce[0].tag === this.opts.casti[1].sloupce[1].tag) {

              } else {
                break;
              }
            // break;

            case this.opts.casti[1].sloupce[1].tag:


              $node = addedTagContainer.tag.cloneNode(true);
              $node.setAttribute('draggable', true);
              $node.addEventListener('click', this._handlerDraggable.bind(this), false);
              $node.addEventListener('dragstart', this._handlerDraggable.bind(this), false);
              $node.addEventListener('dragend', this._handlerDraggable.bind(this), false);
              $node.addEventListener('drag', this._handlerDraggable.bind(this), false);

              tableSelectedItemsBody.appendChild($node);


              break;

            default:
              break;

          }
          break;

        case actionTypes.editor.DELETE_TAG:

          addedTagContainer = action.data.tagContainer;

          // console.log("from DELETE_TAG action", addedTagContainer);

          // simple analytical table tasks
          // const deletedItems = document.querySelectorAll(`[data-tag-id="${addedTagContainer.id}"]`);

          Array.prototype.forEach.call(document.querySelectorAll(`[data-tag-id="${addedTagContainer.id}"]`), ($node) => {

            if (__hasClass($node.parentNode, 'cell-read-only')) {
              // we have to remove the whole row from the table
              // --> cell --> table-row
              __remove($node.parentNode.parentNode);

            } else {
              __remove($node);
            }

          });

          break;

        default:
          break;
      }


    });

  }

  _registerHandlers() {

    this.target.addEventListener('dragover', this._handlerDrop.bind(this), false);
    this.target.addEventListener('dragleave', this._handlerDrop.bind(this), false);
    this.target.addEventListener('drop', this._handlerDrop.bind(this), false);

  }


  _handlerDraggable(e) {

    if (e.type === 'dragstart') {
      e.dataTransfer.setData('text/plain', 'node');
      e.dataTransfer.dropEffect = '';
      e.target.style.opacity = 0.5;

      this.activeDragged = e.target.cloneNode(true);
    }

    if (e.type === 'dragend') {
      e.target.style.opacity = 1;

      // show OK button or feedback
      this.showFeedback();
    }

    return false;

  }

  _handlerDrop(e) {

    if (__hasClass(e.target, 'dashboard-canvas-widget-content')) {

      if (e.type === 'dragover') {

        e.preventDefault();
        e.dataTransfer.dropEffect = '';
        e.target.style.background = '#eee';
      }

      if (e.type === 'dragleave') {

        e.target.style.background = '#fff';

      }

      if (e.type === 'drop') {


        e.preventDefault();

        e.target.style.background = '#fff';
        this.activeDragged.style.opacity = 1;


        this.activeDragged.addEventListener('click', (e) => {

          if (__hasClass(e.target, 'node-close')) {

            e.target.parentNode.remove();

          }

        });

        e.target.appendChild(this.activeDragged);


      }


    }


    return false;

  }


}

AnalyticalTable.defaults = {
  isAdvanced: false,
};


AnalyticalTable._getRowHTML = (nodeHTML, index = 0) => {

  const tableRowTemplate = `
    <div class="table-row">
      <div class="cell cell-read-only cell-border-right">${nodeHTML}</div>
      <div class="cell dashboard-canvas-widget-content"></div>
    </div>
  `;

  return tableRowTemplate;

};

AnalyticalTable._getAdvancedRowHTML = (nodeHTML, index = 0) => {

  const tableRowTemplate = `
    <div class="table-row">
      <div class="cell cell-read-only cell-border-right">${nodeHTML}</div>
      <div class="cell dashboard-canvas-widget-content cell-border-right"></div>
      <div class="cell table-input">
        <input id="table-akteri-vztahy-input-${index}" data-input-target="" data-save="value" type="text" placeholder="Vztah aktérů…" tabindex="-1">
      </div>
    </div>
  `;

  return tableRowTemplate;

};
