import Observer from './observer';
import actionTypes from './store-constants';

class StoreGlobal extends Observer {

  constructor() {

    super();

    /* here the global space for storing objects and data */
    this._data = [];

  }

  _checkExists(id) {
    let objectExists = false;
    this._data.forEach((item, i) => {

      if (item.id === id) {
        objectExists = { item, index: i };
      }

    });

    return objectExists;
  }

  add(data) {

    // console.log('add global called');

    if (this._data.length === 0 || !this._checkExists(data.id)) {

      this._data.push(data);

      store.trigger(actionTypes.textOptions.id, {
        action: {
          type: actionTypes.textOptions.ADD,
        },
        data,
      });

    } else {
      throw new Error('Object already exists! Will not add it.');
    }

  }

  delete(data) {

    const objectExists = this._checkExists(data.id);

    if (objectExists !== false) {

      this._data.splice(objectExists.index, 1);

      store.trigger(actionTypes.textOptions.id, {
        action: {
          type: actionTypes.textOptions.DELETE,
        },
        data,
      });

      return true;

    }
    throw new Error('Tag does not exist! Will not delete it.');


  }

  getAll(opts) {

    if (undefined === opts || !opts.editor) { return this._data; }

    const selectedObjs = this._data.filter(item => item.id === opts.id);

    return selectedObjs;
  }

  getBy(opts) {

    const allowedKeys = ['id'];

    if ((opts === undefined) || (allowedKeys.indexOf(opts.key) === -1)) {
      return false;
    }

    if (!Array.isArray(opts.value)) {
      opts.value = [opts.value];
    }

    const selectedObjs = this._data.filter(item => (item[opts.key] === opts.value[0]) || (item[opts.key] === opts.value[1]));

    return selectedObjs;

  }

  getLast(opts) {

    if (undefined === opts || !opts.editor) {
      return this._data[this._data.length - 1];
    }

    const selectedObjs = this._data.filter(item => item.id === opts.id);

    return selectedObjs[selectedObjs.length - 1];
  }

}

const store = new StoreGlobal();
export default store;
